.container-details {
    background-color: rgb(225,225,225, 0.5);
    background-image: url(/images/backgroud-patas.png);
    display: flex;
    flex-direction: row;
    }


.imagem {
    position: relative;
  }
  .capa {
    display: none;
  }
  
  .imagem:hover .capa {
    display: block;
  }
  
  .capa {
    background: rgb(225,225,225, 0.7);
    width: 100%;
    height: 30%;
    position: absolute;
    bottom: 0;
    color: black;
    padding-top: 1vw ;
    border-bottom-left-radius: 30px !important; 
    border-bottom-right-radius: 30px !important; 
  
  }

  .my-pet {
      display: flex;
      flex-direction: column;
      /* position: fixed; */
  }

  .my-img {
    border-radius: 30px !important; 
    width: 40vw;
    
  }

  .card-img-top {
    height: 20vw;
    object-fit: cover;
    border-radius: 30px !important; 
    /* max-width: 30vw; */
}